<template>
  <div class="beijinTY">
    <!-- <fg-header title="PK竞技场" ></fg-header> -->
     <div class="box">
    <img class='goback' @click="onClickLeft()" src="../../assets/images/goback.png" alt="">
    <div class="daohanlan">
        <p class="text">PK竞技场</p> 
    </div>
  </div>
     <ul class="marT50 listul">
       <li class="listLi">
         <div class="jilu">
           <div class="jilutext clearfix">
             <div class="fl">
               <p>PK数额  {{info.usdt_amount}}</p>
               <p>满员人数 {{info.player_amount}}</p>
             </div>
             <div class="fr">
               <p v-if="this.type!='大厅'">最低账户余额  {{info.usdt_amount}}</p>
               <p>PK成功人数  {{info.player_amount-info.loser_amount}}</p>
             </div>
           </div>
           <div v-if="type=='大厅'||type=='加入'">
             <!-- v-if="info.i_joined" -->
             <div  class='canyupk' @click="gotoPK()">参与PK</div>
           </div>
           <div v-else class="roomid">房间ID：{{id}}</div>
         </div>
       </li>
     </ul>
     <ul class="clearfix margintop30">
       <li class="fl renyuan" v-for="item in waiterlist" :key="item.id">
         <img class="avatar" src='../../assets/images/avatar.png' alt="">
         <div class="textstyle">ID:{{item.invite_code}}</div>
       </li>
     </ul>
      <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">PK进行中</h1>
           <img class="pkzhong" src='../../assets/images/PK.gif' alt="">
           <!-- <div class="btnline">
            <div class="quxiao" @click="popupshow=false">确定</div>
          </div> -->
        </div>
      </van-popup>
      <van-popup v-model="PKshow">
        <div class="PKyaoqingbg" >
          <div class="PKyaoqingbg">
          <h1 class="PKyaoqingtitle">PK{{pkshowText}}</h1>
          <div class="PKcopy_text" v-if="pkshowText=='失败'">
            <p class="yihang" >很遗憾！</p>
            <p>但您仍获得了一把绳子</p>
          </div>
          <div class="PKcopy_text" v-if="pkshowText=='成功'">
            <p>恭喜获得{{usdt_won}}GTF</p>
          </div>
          <div class="PKbtnline">
            <div class='fuzhi' @click="PKshowclick()">确定</div>
          </div>
        </div>
        </div>
      </van-popup>
  </div>
</template>

<script>

import { gameslist_waiter, gamesinfo,gamesjoin,gamesquery ,gamesroomlist_users,game_roomsleave,game_roundsinfo,game_roomsjoin} from '@/api/pk'
  export default {
     data() {
    return {
       popupshow:false,
       PKshow:false,
      id:'',
      info:'',
      waiterlist:[],
      type:'',
      path:"ws://54.193.133.101:3000/cable",
      socket:"",
      pkshowText:'',
      usdt_won:''
    };
  },
   mounted() {
   
    this.id=this.$route.query.id
    this.type=this.$route.query.type
    this.gamesinfo()
    this.gameslist_waiter()
    this.init()
    
  },
  watch(){
 
  },
  // destroyed () {//离开路由页面后执行函数
  //       // 销毁监听
  //      this.socket.onclose = this.close
  //      if(this.type!='大厅'){
  //         game_roomsleave({id:this.id}).then((res) => {
  //           console.log('离开')
  //         }).catch((error) => { reject(error)})
  //      }
      
  // },
  methods: {
     onClickLeft() {
      this.$router.go(-1);
       this.socket.onclose = this.close
       if(this.type!='大厅'){
         let data={
            id:this.id,
            ...this.$datapublic
         }
          game_roomsleave(data).then((res) => {
            console.log('离开')
          }).catch((error) => { reject(error)})
       }
    },
    PKshowclick(){
      this.PKshow=false
       this.$router.push({ path:'/arena'})
    },
    init: function () {
            if(typeof(WebSocket) === "undefined"){
                alert("您的浏览器不支持socket")
            }else{
                // 实例化socket
                this.socket = new WebSocket(this.path)
                // 监听socket连接
                this.socket.onopen = this.open
                // 监听socket错误信息
                this.socket.onerror = this.error
                // 监听socket消息
                this.socket.onmessage = this.getMessage
            }
        },
        open: function () {
            console.log("socket连接成功")
        },
        error: function () {
            console.log("连接错误")
        },
        getMessage: function (msg) {
            console.log(msg.data)
            if(this.id==msg.data.id){
               if(msg.data.action=='join'){
                let obj={
                   invite_code:msg.data.invite_code
                }
                this.waiterlist=this.waiterlist.concat([obj])
              }
              if(msg.data.action=='win'){
                 this.popupshow=true
                 let vm=this
                 setTimeout(() => {
                   game_roundsinfo().then((res) => {
                     if(res.data.code=='success'){
                        vm.popupshow=false
                      if(res.data.data.loser){
                        vm.PKshow=true
                        vm.pkshowText='失败'
                      }else{
                        vm.PKshow=true
                        vm.pkshowText='成功'
                        vm.usdt_won=res.data.data.usdt_won
                      }
                     }else{this.$toast(res.data.msg)}
                     
                    }).catch((error) => { reject(error)})
                   

                 }, 3000);
              }
              if(msg.data.action=='leave'){
                 let newLista = this.waiterlist.filter(itemA => {
                  return itemA.invite_code != msg.data.invite_code
                })
                this.waiterlist = newLista
              }
            }
           
              
        },
        send: function () {
           let data=''
           if(this.type=='大厅'){
             data="game_"+this.id
           }else{
             data="game_room_"+this.id
           }
            this.socket.send(data)
        },
        close: function () {
            console.log("socket已经关闭")
        },
    gamesinfo(){
       let data={
         id:this.id
       }
       if(this.type=='大厅'){
        gamesinfo(data).then((res) => {
          if(res.data.code=='success'){this.info=res.data.data}else{this.$toast(res.data.msg)}
         
          }).catch((error) => { reject(error)})
       }else{
          gamesquery(data).then((res) => {
          if(res.data.code=='success'){this.info=res.data.data}else{this.$toast(res.data.msg)}
          }).catch((error) => { reject(error)})
       }
     
    },
    gameslist_waiter(){
       let data={
         id:this.id
       }
        if(this.type=='大厅'){
          gameslist_waiter(data).then((res) => {
            if(res.data.code=='success'){ this.waiterlist=res.data.data}else{this.$toast(res.data.msg)}
         
          }).catch((error) => { reject(error)})
        }else{
          gamesroomlist_users(data).then((res) => {
          if(res.data.code=='success'){ this.waiterlist=res.data.data}else{this.$toast(res.data.msg)}
          }).catch((error) => { reject(error)})
        }
      
    },
    gotoPK(){
      let data={
        id:this.id,
        ...this.$datapublic
      }
      if(this.type=='大厅'){
          gamesjoin(data).then((res) => {
             if(res.data.code=='success'){ 
               this.gameslist_waiter()
             }else{this.$toast(res.data.msg)}
        
          }).catch((error) => {
            reject(error)
          })
      }else{
         game_roomsjoin(data).then((res) => {
           if(res.data.code=='success'){ 
               this.gameslist_waiter()
             }else{this.$toast(res.data.msg)}
          }).catch((error) => {
            reject(error)
          })
      }
    
          
    }
  }
  }
</script>

<style lang="scss" scoped>
.listul{
  padding-bottom: 30px;
  margin-top: 80px;
}
.jilutext{
  padding-top: 60px;
  padding-left: 80px;
  padding-right: 80px;
}
