  background: url('../../assets/images/quxiao.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 80px;
  font-weight: bold;
  display: inline-block;
  margin-right:15px;
  color:#fff
}
