  width: 212px;
  height: 300px;
  margin: 20px 10px;
}
.avatar{
   width: 212px;
  height: 212px;
}
.textstyle{
    color:26px;
  font-weight: bold;
  text-align: center;
  color:#fff;
  margin-top:25px;
}
.PKyaoqingtitle{
   line-height: 80px;
  font-weight: bold;
  padding-top: 45px;
  font-size: 48px;
  color:#fff;
}

.PKyaoqingbg{
  width:531px;
  height:547px;
  background: url('../../assets/images/tanchuang@2x.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  position: relative;
}
