  margin-top: 80px;
}
.fuzhi{
  width:192px;
  height:100px;
  background: url('../../assets/images/fuzhi.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 100px;
  font-weight: bold;
  display: inline-block;
  margin-left: 25px;
  color:#fff;
  position: absolute;
  bottom: 0;
  left: 155px;
  font-size: 40px;
}
.canyupk{
 width:170px;
  height:70px;
  background: url('../../assets/images/fuzhi.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 70px;
  font-weight: bold;
  display: inline-block;
  margin-left: 25px;
  color:#fff;
  margin-top: 20px;
}
.roomid{
  color:#A53C18;
  font-weight: bold;
  margin-top: 40px;
}
.box{
  display: flex;
  justify-content: flex-start ;
  align-items:center;
  padding-top:40px;
}
.daohanlan{
  width: 370px;
  height: 100px;
  background: url('../../assets/images/daohanlan.png') no-repeat 100% 100%;
  background-size: 100% 100%;
